<template>
  <div class="page users" v-loading="loading">
    <div class="options">
      <el-button type="primary" size="mini" @click="add">新增用户</el-button>
      <el-button type="danger" size="mini">删除</el-button>
    </div>
    <div class="tablebox">
      <el-table
        :data="userData"
        border
        :stripe="true"
        size="small"
        @selection-change="multiple"
        max-height="840"
      >
        <el-table-column type="selection" min-width="60" align="center" />
        <el-table-column type="index" :index="dataIndex" label="序号" align="center" min-width="60" />
        <el-table-column prop="username" label="用户名" align="center" min-width="150" />
        <!-- <el-table-column prop="objectId" label="id" min-width="50"/> -->
        <el-table-column prop="email" label="邮箱" min-width="200" align="center" />
        <el-table-column prop="mobilePhoneNumber" label="手机号" min-width="120" align="center" />
        <el-table-column prop="createdAt" label="创建日期" min-width="150" align="center" />
        <el-table-column prop="updatedAt" label="更新日期" min-width="150" align="center" />
        <el-table-column min-width="150" align="center" label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="edit(scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" @click="del(scope.row)" v-if="!scope.row.auth">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog title="新增用户" :visible.sync="addShow">
      <el-form :model="addForm" label-position="left" label-width="80px">
          <el-form-item label="用户名">
            <el-input v-model="addForm.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input v-model="addForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码">
            <el-input v-model="addForm.repass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="邮箱">
            <el-input v-model="addForm.email" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="电话">
            <el-input v-model="addForm.phone" autocomplete="off"></el-input>
          </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      limit: 20,
      page: 1,
      userData: [],
      currentData: [],
      selectData: [],
      addShow: true,
      addForm: {
        username: '',
        password: '',
        repass: '',
        email: '',
        phone: '',
      }
    };
  },
  mounted() {
    this.loadData();
  },
  methods: {
    loadData() {
      const query = this.Bmob.Query("_User");

      this.loading = true;

      // query.limit(this.limit);
      // query.skip((this.page - 1) * this.limit);
      query.find().then(res => {
        // console.log(res);
        this.loading = false;
        this.userData = res;
      });
    },
    dataIndex(index) {
      return this.page + index;
    },
    edit(row) {
      console.log(row);
    },
    del(row) {
      const self = this;
      //   console.log(row);
      this.$confirm(`是否确定删除该用户：${row.username}？`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        confirmButtonClass: "el-button--danger",
        type: "warning"
      }).then(() => {
        const query = self.Bmob.Query("_User");
        self.loading = true;
        query
          .destroy(row.objectId)
          .then(() => {
            self.loading = false;
            // console.log(res)
            self.$message({
              message: "删除成功！",
              type: "success"
            });
            self.loadData();
          })
          .catch(err => {
            console.log(err);
            self.$message.error(`删除失败！原因：${err.error}`);
          });
      });
    },
    add() {
      this.addShow = true;
    },
    multiple(data) {
      console.log(data);
      this.selectData = data;
    }
  }
};
</script>

