const mass = {
    code(str, setoff) {
        const charArr = str.split('')
        let resArr = []
        charArr.map(item => {
            let ascii = item.charCodeAt()
            resArr.push(String.fromCharCode(ascii + setoff))
        })
        // console.log(resArr.join(''))
        return resArr.join('')
    }
}

export default mass

