<template>
  <div id="app">
    <Nav title="开源说管理平台" :login="login" :username="username" />
    <div class="content">
      <Aside :show="asideShow" />
      <div class="wrap" :class="asideShow ? '' : 'whole'">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import Nav from "@/components/Nav.vue";
import Aside from "@/components/Aside.vue";

export default {
  components: {
    Nav,
    Aside
  },
  data() {
    return {
      asideShow: true,
      login: false,
      username: ""
    };
  },
  methods: {
    getStatus() {
      let current = this.Bmob.User.current();
      if (current === null) {
        this.asideShow = false
        if (["login"].indexOf(this.$route.name) < 0) {
          this.$router.push({ path: "/login"});
        }
      } else {
        this.login = true;
        this.asideShow = true
        this.username = current.username;
      }
    }
  },
  updated() {
    this.getStatus();
  },
  mounted() {
    this.getStatus();
  }
};
</script>


