<template>
  <div class="aside" v-show="show" v-loading="loading">
    <el-tree :data="tree" @node-click="treeClick" />
  </div>
</template>

<script>
export default {
  props: {
    show: {
      type: Boolean
    }
  },
  data() {
    return {
      loading: false,
      tree: []
    };
  },
  methods: {
    treeClick(data) {
      // console.log(data);
      this.$router.push({
        path: data.path,
        params: {
          id: data.lid
        }
      });
    }
  },
  mounted() {
    this.loading = true;
    this.Bmob.Query("Column")
      .find()
      .then(res => {
        this.loading = false;
        // console.log(res);
        const resArr = [];
        res.map(item => {
          resArr.push({
            label: item.name,
            lid: item.objectId,
            path: item.path
          });
        });
        this.tree = resArr;
      })
      .catch(err => {
        this.loading = false;
        this.$message.error(`网络错误，原因：${err.error}`);
      });
  }
};
</script>

<style scoped>
.aside {
  width: 240px;
  height: 100%;
  background-color: #eee;
}
.el-tree {
  background-color: #eee;
}
</style>

